
import './index.scss'
import { Mode } from '../../components/mode/mode'
import DataStore from '@/mobx/data';
import { observer } from 'mobx-react'
import {history} from '&/history'
import Base64 from 'base64-js'
import {Switch,Route,Redirect} from 'react-router-dom';
import LazyLoad from '&/lazyload';


const modes = [
  { text: '图书馆简介', icon: 'icon-loufang', bgcolor: 'rgba(72, 100, 240, 0.6)', fontcolor: '#fff',width:'150px',height:'100px', name: '/index/mode/storeinfo' },
  { text: '公告', icon: 'icon-gonggao', bgcolor: 'rgba(72, 100, 240, 0.6)', fontcolor: '#fff', width:'150px',height:'100px',name: '/index/mode/storenotice' },
  { text: '借还管理', icon: 'icon-jiebei', bgcolor: 'rgba(72, 100, 240, 0.6)', fontcolor: '#fff', width:'150px',height:'100px',name: '/index/mode/borrow' },
  { text: '图书管理', icon: 'icon-book-2', bgcolor: 'rgba(72, 100, 240, 0.6)', fontcolor: '#fff', width:'150px',height:'100px',name: '/index/mode/management' },
  { text: '系统设置', icon: 'icon-xitong', bgcolor: 'rgba(72, 100, 240, 0.6)', fontcolor: '#fff', width:'150px',height:'100px',name: 'system' },
  { text: '个人中心', icon: 'icon-gerenzhongxin1', bgcolor: 'rgba(72, 100, 240, 0.6)', fontcolor: '#fff', width:'150px',height:'100px',name: '/index/mode/mine' },
  { text: '功能新增', icon: 'icon-jia', bgcolor: 'rgba(0, 250, 240, 0.6)', fontcolor: '#fff', width:'150px',height:'100px',name: 'add' },
]

export default class Information extends Component {


  componentDidMount(){
    var usertel = localStorage.getItem('usertel');
    if(usertel){
      usertel = (Base64.toByteArray(usertel)).join('');
      // console.log(usertel)
      DataStore.getUserInfo({
        url:'/bookstore/getUserInfo',
        params:{
          usertel
        }
      })
    }else{
      history.push('/login')
    }
  }

  render() {
    return (
      <div>
        <div className="main">
          <h2 className="title">星辰图书馆</h2>
          <div className='con'>
            <div className="modelist">
              {
                modes.map((m, i) => {
                  return (
                    <Mode mystyle={{flexDirection:'column',justifyContent:'center'}} key={i} msg={m}></Mode>
                  )
                })
              }
            </div>
          </div>
        </div>
      </div>
    )
  }
}





