

import {observable,action} from 'mobx'

import {axios} from '@/utils/axios'


class DataStore{
  @observable touxiang = require('@/assets/default.jpg');
  @observable username = '尚未登陆';
  @observable usertel = '';
  @observable files = [];
  @observable borrowbookinfo={};

  @action setAvatar = ({url,data}) =>{
    axios.post(url,data).then(res=>{
      // console.log((res.data.pic).replace(/public/,'http://localhost:1906'))
      if(res.data.pic){
        this.touxiang = (res.data.pic).replace(/public/,'https://xx1314.fun:8001')
      }
      
    })
  }

  @action getUserInfo = ({url,params})=>{
    axios.get(url,{params}).then(res=>{
      // console.log(res.data.result)
      this.touxiang = res.data.result.avatar.replace(/public/,'https://xx1314.fun:8001');
      this.username = res.data.result.username;
      this.usertel = res.data.result.usertel;
    })
  }

  @action saveBookImgs = (files)=>{
    this.files = files
  }

  @action changeBookImg =()=>{
    this.files.length=0;
  }
  @action setBookImg=(imgs)=>{
    this.files = imgs
  }


  //借书----查询剩余书籍
  @action getBookInfoById = ({url,params},cb)=>{
    axios.get(url,{params}).then(res=>{
      this.borrowbookinfo = res.data.result;
      cb(res.data.result)
    })
  }


  //改变书的数量
  @action changBookCount=(num)=>{
    this.borrowbookinfo.count -=num;
  }

}

export default new DataStore();